@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
body
{   
    display: flex;
    justify-content: center;
    background-color: #1c1e22;
    height: 100vh;
}

header {
  display: flex;
  justify-content: center;
}

.logo {
  margin-top: 40px;
  align-items: center;
}

.test-box{
    background-color: #1c1e22;
  width: 250px;
  height: 175px;
}
.members {
  margin-top: -50px;
  padding: 130px;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
}
.member {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  width: 250px;
  height: 180px;
  border-radius: 20px;
}

.member:hover {
  position: absolute;
  z-index: 1;
  border-radius: 30px 30px 20px 20px;
  width: 500px;
  height: 355px;
  box-shadow: 0 0 70px rgba(0,0,0,0.8);
  cursor: pointer;
  transition: 0.3s;
  transform: translate(-25%, -87px);
}
.profile-img {
    width: 120px;
    /* height: 120px; */
    border-radius: 50%;
    /* left: 50%;
    top: -25px; */
    transform: translatey(-85%);
    box-shadow: 0 6px 30px 0 rgba(0, 0, 0, 0.5);
}

.member:hover .profile-img{
  transition: 0.3s;
  outline: 5px solid white;
  margin-right: 130px;
  box-shadow: none;
  transform: translate(-50%, -20%);
} 

.nameBox {
  display: flex;
  flex-direction: column;
  height: 47px;
  justify-content: center;
  transform: translatey(-190%);
  line-height: 1.25em;
  text-align: center;
  color: #555;
  font-weight: 700;
  font-size: 1.1em;
  left: 50%;
}

.nameBox span {
  font-size: 0.75em;
  opacity: 50%;
}

.member:hover .nameBox {
  width: 250px;
  transition: 0.3s;
  transform: translate(-125px, -20%);
}

.banner {
  object-fit: cover;
  visibility: hidden;
  width: 100%;
  border-radius: 20px 20px 0 0;
}

.member:hover .banner {
  visibility: visible;
}

.info {
  /* width: 250px; */
  /* background-color: cadetblue; */
  /* margin-right: 20px; */
  visibility: hidden;
  position: absolute;
  width: 250px;
  height: 188px;
  right: 0;
  bottom: 0;
  line-height: 1.2em;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 30px;
  font-weight: 100;
  color: #555;
  opacity: 80%;
}

.member:hover .info {
  visibility: visible;
}
